<template>
  <v-row class="px-3 mb-12 pb-12">
    <v-snackbar v-model="snackbar.show" top :color="snackbar.color" right>
      {{snackbar.message}}
      <v-btn text @click="snackbar.show = false">Close</v-btn>
    </v-snackbar>
    <v-col>
      <v-card-title>
        Items
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>

      <v-data-table
        :search="search"
        :headers="headers"
        :items="items"
        :items-per-page="2"
        class="elevation-1"
        :footer-props="{
            'items-per-page-options': [25, 50, 100, -1]
        }"
        @current-items="filter"
      >
        <template v-slot:item.enabled="{ item }">
          <v-simple-checkbox v-model="item.enabled" disabled></v-simple-checkbox>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </v-col>

    <v-col cols="12" class="text-right">
      <download-excel
        class="v-btn v-btn--depressed theme--light v-size--small"
        worksheet="Worksheet"
        name="products.xls"
        :data="json_data"
        :fields="json_fields"
      >
        <v-icon>mdi-upload</v-icon>Export
      </download-excel>
    </v-col>

    <!-- Delete Dialog -->
    <v-dialog v-model="destroy.destroy" max-width="512px">
      <v-card>
        <v-card-title class="headline">Delete {{destroy.item.name}}</v-card-title>
        <v-card-text>Are you sure that you want to permanently delete {{destroy.item.name}} ?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="resetDelete">Cancel</v-btn>
          <v-btn color="red darken-1" text @click="confirmDelete">Delete Permanently</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Edit Dialog -->
    <v-dialog v-model="edit.edit" max-width="768px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Item</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <v-form ref="edit" v-model="valid" lazy-validation>
                <v-row>
                  <v-col cols="6" class="py-0">
                    <v-text-field
                      prepend-inner-icon="mdi-label-multiple-outline"
                      v-model="edit.item.name"
                      :counter="100"
                      :rules="nameRules"
                      label="Name"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3" class="py-0">
                    <v-select
                      prepend-inner-icon="mdi-folder-outline"
                      v-model="edit.item.category_id"
                      :items="categories"
                      label="Category"
                    ></v-select>
                  </v-col>

                  <v-col cols="3" class="py-0">
                    <v-select
                      prepend-inner-icon="mdi-percent"
                      v-model="edit.item.tax_id"
                      :items="taxes"
                      label="Tax"
                    ></v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-textarea
                      :auto-grow="false"
                      label="Description"
                      rows="3"
                      v-model="edit.item.description"
                    ></v-textarea>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="3" class="py-0">
                    <v-text-field
                      prepend-inner-icon="mdi-cash-multiple"
                      v-model="edit.item.sale_price"
                      :rules="saleRules"
                      label="Sale Price"
                      type="number"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3" class="py-0">
                    <v-text-field
                      prepend-inner-icon="mdi-cash-multiple"
                      v-model="edit.item.purchase_price"
                      :rules="purchaseRules"
                      label="Purchase Price"
                      type="number"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3" class="py-0">
                    <v-text-field
                      prepend-inner-icon="mdi-package"
                      v-model="edit.item.quantity"
                      :rules="quantityRules"
                      label="Quantity"
                      type="number"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3" class="py-0">
                    <v-select
                      prepend-inner-icon="mdi-scale-balance"
                      v-model="edit.item.unit_id"
                      :items="units"
                      label="Unit"
                    ></v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col class="py-0">
                    <v-checkbox v-model="edit.item.enabled" label="Enabled"></v-checkbox>
                  </v-col>
                </v-row>
              </v-form>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="resetEdit">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveEdit">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from "axios";

export default {
  props: ["items", "units", "categories", "taxes"],
  data() {
    return {
      snackbar: {
        show: false,
        color: "",
        message: ""
      },
      edit: {
        edit: false,
        index: null,
        item: {}
      },
      destroy: {
        destroy: false,
        index: null,
        item: {}
      },
      search: "",
      headers: [
        {
          text: "NAME",
          align: "start",
          sortable: true,
          value: "name"
        },
        { text: "CATEGORY", value: "category" },
        { text: "QTY", value: "quantity" },
        { text: "UNIT", value: "unit" },
        { text: "SALE", value: "sale_price" },
        { text: "PURCHASE", value: "purchase_price" },
        { text: "TAX", value: "tax" },
        { text: "ENABLED", value: "enabled" },
        { text: "ACTIONS", value: "actions", sortable: false }
      ],

      valid: true,

      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 100) || "Name must be less than 100 characters"
      ],
      saleRules: [
        v => !!v || "Sale Price is required",
        v => (v && v >= 0) || "Sale Price must not be less than 0."
      ],
      purchaseRules: [
        v => !!v || "Purchase Price is required",
        v => (v && v >= 0) || "Purchase Price must not be less than 0."
      ],
      quantityRules: [
        v => !!v || "Quantity is required",
        v => (v && v >= 0) || "Quantiry must not be less than 0."
      ],
      json_data: [],
      json_fields: {
        Name: "name",
        Description: "description",
        "Sale Price": "sale_price",
        "Purchase Price": "purchase_price",
        Qty: "quantity",
        Unit: "unit",
        Category: "category",
        Tax: "tax"
      }
    };
  },

  methods: {
    /* Excel */
    filter(data) {
      this.json_data = data;
    },
    editItem(item) {
      this.edit.item = item;
      this.edit.index = this.items.indexOf(item);
      axios.get(`/api/items/${item.id}`).then(res => {
        this.edit.item = res.data;
        this.edit.item.purchase_price += "";
        this.edit.item.sale_price += "";
        this.edit.item.quantity += "";
      });
      this.edit.edit = true;
    },

    resetEdit() {
      this.edit.item = {};
      this.edit.index = null;
      this.edit.edit = false;
    },

    saveEdit() {
      if (this.$refs.edit.validate()) {
        axios
          .put(`/api/items/${this.edit.item.id}`, this.edit.item)
          .then(res => {
            if (res.status == 200) {
              this.snackbar.show = true;
              this.snackbar.color = "success";
              this.snackbar.message = `200 (${res.data.data.name} Updated Successfully)`;
              this.$emit("update", {
                index: this.edit.index,
                data: res.data.data
              });
            } else {
              this.snackbar.show = true;
              this.snackbar.color = "error";
              this.snackbar.message = "500 (Internal Server Error)";
            }
          })
          .catch(err => {
            console.log(err);
            this.snackbar.show = true;
            this.snackbar.color = "error";
            this.snackbar.message = "500 (Internal Server Error)";
          })
          .finally(() => {
            this.resetEdit();
          });
      }
    },

    deleteItem(item) {
      this.destroy.item = item;
      this.destroy.index = this.items.indexOf(item);
      this.destroy.destroy = true;
    },

    resetDelete() {
      this.destroy.item = {};
      this.destroy.index = null;
      this.destroy.destroy = false;
    },

    confirmDelete() {
      if (this.destroy.destroy && this.destroy.index > -1) {
        axios
          .delete(`/api/items/${this.destroy.item.id}`)
          .then(res => {
            this.snackbar.show = true;
            if (res.status == 200) {
              if (res.data.status == 412) {
                this.snackbar.color = "error";
                this.snackbar.message = `412 Precondition Failed`;
              } else {
                this.snackbar.color = "success";
                this.snackbar.message = `200 (${res.data.name} Deleted Successfully)`;
                this.items.splice(this.destroy.index, 1);
              }
            } else {
              this.snackbar.color = "error";
              this.snackbar.message = "500 (Internal Server Error)";
            }
          })
          .catch(() => {
            this.snackbar.show = true;
            this.snackbar.color = "error";
            this.snackbar.message = "500 (Internal Server Error)";
          })
          .finally(() => {
            this.destroy.item = {};
            this.destroy.index = null;
            this.destroy.destroy = false;
          });
      }
    }
  }
};
</script>
